.about__container {
  background: #ffb001;
  border-radius: 10px;
  width: 100%;
  height: 314px;
  padding-top: 51px;
}

.about__container__title {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #212121;
  margin: 0px 40px 25px 38px;
}

.about__container__description {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  margin: 26px 21px 75px 19px;
  text-align: center;
  color: #212121;
}

@media screen and (min-width: 1440px) {
  .about__container {
    border: 20px solid #ffffff;
    box-sizing: border-box;
    border-radius: 30px;
    padding-top: 58px;
    height: 365px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__title {
      margin: 0 auto;
      width: 763px;
      font-size: 40px;
      line-height: 49px;
    }
    &__description {
      font-size: 20px;
      line-height: 32px;
      width: 711px;
      margin: 39px 0 39px 18px;
    }
  }
}
